import Modal from 'bootstrap/js/dist/modal'

//  modal 
export default {
  methods: {
    openModal () {
      this.modal.show()
    },
    hideModal () {
      this.modal.hide()
    }
  },
  mounted () {
    //  modal
    //  ref="modal"  Modal 
    //  assign  data  modal
    this.modal = new Modal(this.$refs.modal)
  }
}
